import { SubmitHandler, useForm } from 'react-hook-form';
import {
  LoginDiv,
  LoginSubmitBtn,
} from 'design/loginStyles/EmailLoginPageStyles';
import {
  Form,
  Input,
  InputLabel,
  PageContainer,
  Title,
} from 'design/commonStyles';
import { useMutation } from '@tanstack/react-query';
import { fetchEmailLogin } from 'api/loginApi';
import { useNavigate } from 'react-router-dom';
import { ReactComponent as RoommateLogo } from 'assets/roommate.svg';
import { SignUpPageContainer } from 'design/signupStyles/SignUpStyle';
import Loader from 'components/loader/Loader';

type FormValue = {
  email: string;
  password: string;
};

function EmailLoginPage() {
  const {
    register,
    handleSubmit,
    formState: { isValid, errors },
  } = useForm<FormValue>({ mode: 'all' });

  const { mutate: loginMutation, isLoading } = useMutation(fetchEmailLogin, {
    onSuccess: ({ data }) => {
      if (data.code === 200) {
        console.log(data);
        let accessToken = data.message.split(' ')[0];
        let refreshToken = data.message.split(' ')[1];
        sessionStorage.setItem('accessToken', accessToken);
        sessionStorage.setItem('refreshToken', refreshToken);
        sessionStorage.setItem('userId', data.id);
        navigation('/');
      } else {
        alert(data.message);
      }
    },
  });

  const navigation = useNavigate();

  const onValid: SubmitHandler<FormValue> = data => {
    loginMutation(data);
  };

  if (isLoading) {
    return (
      <PageContainer style={{ justifyContent: 'center' }}>
        <Loader />
      </PageContainer>
    );
  }

  return (
    <SignUpPageContainer>
      <Title>
        <RoommateLogo height={44} />
        <p>
             ! 
          <br />
          <span>    </span> <br />
              !
        </p>
      </Title>
      <Form onSubmit={handleSubmit(onValid)}>
        <InputLabel htmlFor="email"></InputLabel>
        <LoginDiv>
          <Input
            id="email"
            placeholder=" ."
            {...register('email', {
              required: true,
              pattern: {
                value:
                  /^[0-9a-zA-Z]([-_\.]?[0-9a-zA-Z])*@[0-9a-zA-Z]([-_\.]?[0-9a-zA-Z])*\.[a-zA-Z]{2,3}$/,
                message: '   .',
              },
            })}
          />
        </LoginDiv>
        <span>{errors?.email?.message}</span>
        <InputLabel htmlFor="password"></InputLabel>
        <LoginDiv>
          <Input
            id="password"
            type="password"
            placeholder=" ."
            {...register('password', {
              required: true,
              pattern: {
                value: /^(?=.*[A-Za-z])(?=.*\d)[A-Za-z\d]{8,}$/i,
                message:
                  '    8  .',
              },
            })}
          />
        </LoginDiv>
        <span>{errors?.password?.message}</span>
        <LoginSubmitBtn type="submit" disabled={!isValid}>
          
        </LoginSubmitBtn>
      </Form>
    </SignUpPageContainer>
  );
}

export default EmailLoginPage;
