import React, { useState } from 'react';
import { MyIntroduceOptionBox } from 'design/myPageStyles/myIntroduceSelfStyles';
import { useNavigate } from 'react-router-dom';
import { useMutation } from '@tanstack/react-query';
import { postFirstRegisterHouseInfo } from 'api/mypageApi';
import {
  LocationSelect,
  SignUpPageContainer,
  SignUpSection,
  SignUpBtn,
} from 'design/signupStyles/SignUpStyle';
import { Form, Input, InputLabel, Title } from 'design/commonStyles';
import { SubmitHandler, useForm } from 'react-hook-form';
import { ReactComponent as RoommateLogo } from 'assets/roommate.svg';
import { locationData } from 'utils/locationData';

const roomCount: Array<String> = ['', '1', '2', '3', '3 '];
const costRange = [...new Array(10)].map((_, i) => (i + 1) * 10);
const period = [...new Array(14)].map((_, i) => i);

export interface FormValue {
  cost: string;
  experience: string;
  houseInfo: string;
  info: string;
  room: string;
  want_long: string;
  location: string;
  dormitory: string;
}

function RegisterInfoPage() {
  const {
    register,
    handleSubmit,
    formState: { errors, isValid },
  } = useForm<FormValue>({ mode: 'all' });
  const navigation = useNavigate();
  const [formStep, setFormStep] = useState(1);

  const mutation = useMutation(postFirstRegisterHouseInfo, {
    onSuccess({ data }) {
      if (data.code == 200) {
        alert('   .   .');
        navigation('/residential-test');
      } else if (data.code == 409) {
        alert(
          '  .      .'
        );
      } else {
        alert(data.error);
      }
    },
  });

  const nextPage = () => {
    setFormStep(prev => prev + 1);
  };

  const onValid: SubmitHandler<FormValue> = data => {
    mutation.mutate(data);
    sessionStorage.removeItem('email');
    sessionStorage.removeItem('password');
  };

  return (
    <SignUpPageContainer>
      {formStep === 1 ? (
        <Title>
          <RoommateLogo height={44} />
          <p>    !</p>
        </Title>
      ) : formStep === 2 ? (
        <Title>
          <RoommateLogo height={44} />
          <p>    !</p>
        </Title>
      ) : (
        <Title>
          <RoommateLogo height={44} />
          <p>     !</p>
        </Title>
      )}

      <Form onSubmit={handleSubmit(onValid)}>
        {formStep === 1 && (
          <SignUpSection>
            <InputLabel htmlFor="period">  </InputLabel>
            <LocationSelect
              id="experience"
              {...register('experience', {
                required: true,
              })}
              defaultValue={''}
            >
              <option value="" disabled hidden>
                   .
              </option>
              {period.map((period, index) => (
                <MyIntroduceOptionBox value={period} key={index}>
                  {period < 13 ? `${period}` : '1 '}
                </MyIntroduceOptionBox>
              ))}
            </LocationSelect>
            <InputLabel htmlFor="want_long">  </InputLabel>
            <LocationSelect
              id="want_long"
              {...register('want_long', { required: true })}
              defaultValue={''}
            >
              <option value="" disabled hidden>
                   .
              </option>
              {period.map((period, index) => (
                <MyIntroduceOptionBox value={period} key={index}>
                  {period < 13 ? `${period}` : '1 '}
                </MyIntroduceOptionBox>
              ))}
            </LocationSelect>
          </SignUpSection>
        )}

        {formStep === 2 && (
          <SignUpSection>
            <InputLabel htmlFor="location"></InputLabel>
            <LocationSelect
              id="location"
              {...register('location', { required: true })}
              defaultValue={''}
            >
              <option value="" disabled hidden>
                 
              </option>
              {locationData.map((data, index) => (
                <option value={data} key={index}>
                  {data}
                </option>
              ))}
            </LocationSelect>
            <InputLabel htmlFor="dormitory"></InputLabel>
            <Input
              id="dormitory"
              type="dormitory"
              {...register('dormitory', { required: true })}
              placeholder="ex) or "
            />
            <InputLabel htmlFor="room"> </InputLabel>
            <LocationSelect
              id="room"
              {...register('room', { required: true })}
              defaultValue=""
            >
              <option value="" disabled hidden>
                     .
              </option>
              {roomCount.map((roomCount, index) => (
                <MyIntroduceOptionBox value={index}>
                  {roomCount}
                </MyIntroduceOptionBox>
              ))}
            </LocationSelect>
            <InputLabel htmlFor="cost"></InputLabel>
            <LocationSelect
              id="cost"
              {...register('cost', { required: true })}
              defaultValue=""
            >
              <option value="" disabled hidden>
                   .
              </option>
              {costRange.map(costRange => (
                <MyIntroduceOptionBox value={costRange * 10000} key={costRange}>
                  {costRange < 100000000 ? `${costRange} ` : '100 '}
                </MyIntroduceOptionBox>
              ))}
            </LocationSelect>
            <InputLabel htmlFor="houseInfo"> </InputLabel>
            <Input
              id="houseInfo"
              {...register('houseInfo', {
                required: '  !',
              })}
            />
            <span>{errors.houseInfo?.message}</span>
          </SignUpSection>
        )}

        {formStep === 3 && (
          <SignUpSection>
            <InputLabel htmlFor="info"> </InputLabel>
            <Input
              id="info"
              {...register('info', {
                required: '  !',
                maxLength: {
                  value: 50,
                  message: '50    ',
                },
              })}
            />
            <span>{errors.info?.message}</span>
          </SignUpSection>
        )}

        {formStep === 3 ? (
          <SignUpBtn disabled={!isValid}> </SignUpBtn>
        ) : (
          <SignUpBtn disabled={!isValid} onClick={nextPage}>
            
          </SignUpBtn>
        )}
      </Form>
    </SignUpPageContainer>
  );
}

export default RegisterInfoPage;
